@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vmin;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--sec {
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch__hand--min {
  z-index: 0;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand--sec {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand--min {
  animation-duration: 600s;
}
